export default {
	props: {
		bookItem: {
			type: Object,
			default() {
				return {}
			}
		}
	},

	methods: {
		onBookTap(e) {
			let bookid = e.currentTarget.dataset.bookid

			// 跳转到书单详情页
			uni.navigateTo({
				url: `/pages/book-detail/book-detail?bookid=${bookid}`
			})
		}
	}
}
